
export function getCookie(cname) {
    var name = cname + "=";
    var decodedCookie = decodeURIComponent(document.cookie);
    var ca = decodedCookie.split(';');
    for(var i = 0; i <ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return null;
}

export function setCookie(name, value) {
    document.cookie = name + "=" + value + "; path=/";
}

export function deleteCookie(name) {
    document.cookie = name + '=; expires=Thu, 01 Jan 1970 00:00:01 GMT;';
}

export function loadScript(path, scriptId) {
    return new Promise((resolve, reject) => {
        let script = document.createElement('script');
        script.onload = function() {
            resolve();
        };
        script.onerror = function() {
            reject();
        };
        script.setAttribute('src', path);
        script.setAttribute('id', scriptId);
        document.head.appendChild(script);
    });
}

export function unloadScript(id) {
    const el = document.querySelector('script[id="' + id + '"]');
    if (el != null) {
        document.head.removeChild(el);
    }
}

export function loadEngine(version) {
    let scriptId = "engine";
    let enginePath = `/mmg/js/mmg/${version}/mmg.js`;
    //TODO: carregar o script somente se ainda não existir, ou se for de uma versão diferente
    unloadScript(scriptId);
    return loadScript(enginePath, scriptId);
}
