import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    meta: {
      title: 'Easy Game Maker'
    },
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    meta: {
      title: 'Easy Game Maker - About'
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "group-1" */ '../views/About.vue')
  },
  {
    path: '/sign-up',
    name: 'SignUp',
    meta: {
      title: 'Easy Game Maker - Sign-up'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/SignUp.vue')
  },
  {
    path: '/sign-in',
    name: 'SignIn',
    meta: {
      title: 'Easy Game Maker - Sign-in'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/SignIn.vue')
  },
  {
    path: '/games',
    name: 'Games',
    meta: {
      title: 'Easy Game Maker - Games'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/Games.vue')
  },
  {
    path: '/create',
    //name: 'GameEditor',
    meta: {
      title: 'Easy Game Maker - Create Game'
    },
    component: () => import(/* webpackChunkName: "gameeditor-v0.1.3" */ '../views/engine/v0.1.3/GameEditor.vue')
  },
  {
    path: '/create/snapshot',
    //name: 'GameEditor',
    meta: {
      title: 'Easy Game Maker - Create Game'
    },
    component: () => import(/* webpackChunkName: "gameeditor-snapshot" */ '../views/engine/snapshot/GameEditor.vue')
  },
  {
    path: '/create/v0.1.3',
    meta: {
      title: 'Easy Game Maker - Create Game'
    },
    component: () => import(/* webpackChunkName: "gameeditor-v0.1.3" */ '../views/engine/v0.1.3/GameEditor.vue')
  },
  {
    path: '/play/snapshot/:id',
    //name: 'PlayGame',
    meta: {
      title: 'Easy Game Maker - Play Game'
    },
    component: () => import(/* webpackChunkName: "playgame-snapshot" */ '../views/engine/snapshot/PlayGame.vue')
  },
  {
    path: '/play/v0.1.3/:id',
    meta: {
      title: 'Easy Game Maker - Play Game'
    },
    component: () => import(/* webpackChunkName: "playgame-v0.1.3" */ '../views/engine/v0.1.3/PlayGame.vue')
  },
  {
    path: '/my-games',
    name: 'MyGames',
    meta: {
      title: 'Easy Game Maker - My Games'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/MyGames.vue')
  },
  {
    path: '/my-profile',
    name: 'MyProfile',
    meta: {
      title: 'Easy Game Maker - My Profile'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/MyProfile.vue')
  },
  {
    path: '/logout',
    name: 'Logout',
    meta: {
      title: 'Easy Game Maker - Logout'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/Logout.vue')
  },
  {
    path: '/support-us',
    name: 'SupportUs',
    meta: {
      title: 'Easy Game Maker - Support Us'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/SupportUs.vue')
  },
  {
    path: '/contact-us',
    name: 'ContactUs',
    meta: {
      title: 'Easy Game Maker - Contact Us'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/ContactUs.vue')
  },
  {
    path: '/help',
    name: 'Help',
    meta: {
      title: 'Easy Game Maker - Help'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/Help.vue')
  },
  {
    path: '/features',
    name: 'Features',
    meta: {
      title: 'Easy Game Maker - Features'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/Features.vue')
  },
  {
    path: '*',
    name: 'NotFound',
    meta: {
      title: 'Easy Game Maker - Not Found'
    },
    component: () => import(/* webpackChunkName: "group-1" */ /* webpackPrefetch: true */ '../views/NotFound.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  routes: routes
})

// Para atualizar o <title> das páginas
router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
})

// Google Analytics - track virtual pageviews
router.afterEach((to) => {
  
  if (process.env.VUE_APP_GOOGLE_ANALYTICS_ENABLED=="true") {

    // eslint-disable-next-line no-undef
    gtag('config', process.env.VUE_APP_GOOGLE_ANALYTICS_TRACKING_ID, {
      'page_path': to.fullPath,
      'page_title' : to.meta.title,
      'send_page_view': true
    });

  }

});

export default router
